.modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, .5);
    display: flex;
    justify-content: center;
    z-index: 1;
}

.modal-body {
    margin: auto;
    padding: 2rem;
    height: 200px;
    width: 400px;
    border-radius: 5px;
    background: #fff;
}